<template>
	<div class="overflow-auto" data-ss="aaa">
		<h4 class="text-left">Table.vue</h4>	
		<div id="btnGroup" data-ss="zz" class="text-right">
		<b-button-group>
			<template v-for="(btn, idx) in buttons">
			<b-button
				v-if="btn.routeName !== 'ent'"
				:key="idx"
				:route-name="btn.routeName"
				:route-url="btn.routeUrl"
				:variant="btn.variant"
				@click="getTableItemsBy(btn.routeName, btn.caption)"
			>
			{{ btn.caption }}
			</b-button>
			
			<b-dropdown 
				v-else
				right
				:key="idx"
				text="Entity Info"
			>
				<b-dropdown-item @click="getTableItemsBy('ent-pat', 'Patent Info', 'chn')">Patent</b-dropdown-item>
				<b-dropdown-item @click="getTableItemsBy('ent-pro', 'Project Info')">Project</b-dropdown-item>
				<b-dropdown-item @click="getTableItemsBy('ent-the', 'Thesis Info')">Thesis</b-dropdown-item>
			</b-dropdown>
			</template>
		</b-button-group>
		</div>
		
			
		<!-- <h4 class="text-center">{{tableTitle}}</h4> -->
		<b-table striped hover caption-top
			id="my-talbe"
			:items="items"
			:per-page="perPage"
			:current-page="currentPage"
			small
		>
			<template slot="table-caption">
				<h4 class="text-center">{{tableTitle}}</h4>
			</template>
		
		</b-table>
		
		<div class="offset-9 text-right" v-show="rows > perPage" >
		<!-- <p class=""> -->
			<span>Total items: {{ rows }},</span>&nbsp;
			<span>Current Page: {{ currentPage }}</span>
			<br>
			<!-- <span>每页行数:</span> -->
			<label>每页行数:</label>
			<b-form-select v-model="perPage" :options="options"></b-form-select>	
		<!-- </p> -->
		
		</div>
		<br>
		<b-pagination
			v-model="currentPage"
			v-show="rows > perPage"
			align="right"
			:total-rows="rows"
			:per-page="perPage"
			aria-controls="my-table"
		></b-pagination>
	</div>
</template>

<script>
import Vue from 'vue';

//Importing as a Vue.js plugin
//import { ButtonGroupPlugin } from 'bootstrap-vue';
//Vue.use(ButtonGroupPlugin);

//import { ButtonPlugin } from 'bootstrap-vue';
//Vue.use(ButtonPlugin);

//Importing individual components
//register them globally
import { BButtonGroup } from 'bootstrap-vue';
Vue.component('b-button-group', BButtonGroup);

import { BButton } from 'bootstrap-vue';
Vue.component('b-button', BButton);

import { BPagination } from 'bootstrap-vue';
Vue.component('b-pagination', BPagination);

import { BTable } from 'bootstrap-vue';
Vue.component('b-table', BTable);

import { BFormSelect } from 'bootstrap-vue'
Vue.component('b-form-select', BFormSelect)

//var host=document.location.host;

const routeConf=[
	{name:'serv',url:'/serv'},
	{name:'conf',url:'/conf'},
	{name:'env',url:'/env'},
	{name:'ent',url:'/ent'},
	{name:'ent-pat',url:'/ent/pat'},
	{name:'ent-pro',url:'/ent/pro'},
	{name:'ent-the',url:'/ent/the'},
];

//const postmanUrl='https://a9baf5bf-a2cf-43a8-8db8-2fb9aa3f5acc.mock.pstmn.io';
const BankEndUrl='http://localhost:8090';

const entFieldName={
	pat:{
		patnum:{chn:'专利编号'},
		topic:{chn:'题目'},
		type:{chn:'类型'},
		patowner:{chn:'所有人'},
		inventor:{chn:'发明人'},
		author:{chn:'撰写人'},
	},
	the:{},
	pro:{}
};

const btnItems=[
	{variant:'success',caption:'Initial Info',routeName:'',routeUrl:''},
	{variant:'info',caption:'Server Info',routeName:'serv',routeUrl:'/serv'},
	{variant:'warning',caption:'Config Info',routeName:'conf',routeUrl:'/conf'},
	{variant:'primary',caption:'Envirement Info',routeName:'env',routeUrl:'/env'},
	{variant:'dark',caption:'Entity Info',routeName:'ent',routeUrl:'/ent'},
];

const initItems=[
    { 'No.':1, age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
    { 'No.':2, age: 21, first_name: 'Larsen', last_name: 'Shaw' },
    { 'No.':3, age: 89, first_name: 'Geneva', last_name: 'Wilson' },
    { 'No.':4, age: 38, first_name: 'Jami', last_name: 'Carney' }
];

var getUrl=(routeName)=>{
		let url='';
		for(var n=0;n<routeConf.length;n++){
			if(routeName==routeConf[n].name){
				url=routeConf[n].url;
				break;
			}
		}
		return url;
	};

var getEntFieldName=(entType,lang='')=>{
	let ent=Object.keys(entFieldName).includes(entType)?entType:'';
	let langObj=ent?entFieldName[ent]:{};
	let resObj={};
		
	if(lang && Object.keys(langObj).length){
		for(var key in langObj){
			resObj[key]=langObj[key][lang];
		}
	}
	// console.log('getEntFieldName(): resObj');
	// console.table(resObj);
	return resObj;
};

async function fetchData(url,opts={method: 'GET'}){
	let typeArr=['blob','text','json','formData'];
	//js原生fetch()方法的第二参数
	let optDefault={
			method: '', // *GET, POST, PUT, DELETE, etc
			//mode: 'cors', // no-cors, cors, *same-origin
			//cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
			//credentials: 'same-origin', // include, *same-origin, omit
			//headers: {
				//'Content-Type': 'application/json',
				// 'Content-Type': 'application/x-www-form-urlencoded',
			//},
			//redirect: 'follow', // manual, *follow, error
			//referrer: 'no-referrer', // no-referrer, *client
			//body:JSON.stringify({zz:11}), // body data type must match "Content-Type" header while method=='POST' or 'PUT'
			type:'json',
		};
	let opt=Object.assign(optDefault,opts);
	let res={ok:false};
	let result={ok:false,cont:{}};
	
	if(url && typeArr.includes(opt.type)){
		res=await fetch(url,opt);
		//console.log(res);
	}
	
	if(res.ok){
		result.ok=true;
		switch(opt.type){
			case 'blob':
				result.cont=await res.blob();
				break;
			case 'text':
				result.cont=await res.text();
				break;
			case 'formData':
				result.cont=await res.formData();
				break;
			default:
				result.cont=await res.json();
				break;
		}
		
	}
	
	
	//默认返回的是json对象
	return result;
}

export default {
    name: 'TheTable',
	//data() {
    data:function() {
      return {
        tableTitle:'Initial Info',
		//表格表头设置
		//fields:()={
		//	return
		//},
		items: initItems,
		perPage: 10,
        currentPage: 1,
		rows:initItems.length,
		buttons:btnItems,
		options: [
			{ value: 5, text: '5' },
			{ value: 10, text: '10' },
			{ value: 20, text: '20' },
			{ value: 50, text: '50', }
		]
		}
	},
	methods: {
		getTableItemsBy: function (routeName,tableTitle,itemLang='') {
			//let url=getUrl(routeName)?BankEndUrl+getUrl(routeName):'';
			let url=routeName?getUrl(routeName):'';
			//let url=getUrl(routeName)?postmanUrl:'';
						
			this.tableTitle=tableTitle;
			this.perPage=10;
				
			//console.log('getTableItemsBy');
			
			//fetchData(url,{method:'GET',type:'json'})
			fetchData(url,{method:'GET',type:'json'})
			.then(result=>{
				let res=result.ok;
				let cont=result.cont;
				let ent=cont.hasOwnProperty('ent')?cont['ent']:'';
				let list=ent&&cont.hasOwnProperty('list')?cont['list']:[];
				let itemObj=ent?getEntFieldName(ent,itemLang):{};
				let arr=[];
				
				if(!res){
					this.items=initItems;
					this.rows=initItems.length;
					return
				}
				
				if(ent){
					//数组list的值为对象
					for(var i=0;i<list.length;i++){
						arr[i]={};
					
						if(itemLang=='chn'){
							arr[i]['序号']=i+1;
						}else{
							arr[i]['No.']=i+1;
						}
						for(var k in list[i]){
							if(Object.keys(itemObj).includes(k)){
								arr[i][itemObj[k]]=list[i][k];
							}else{
								arr[i][k]=list[i][k];
							}
						}
					}
					
					if(!list.length){
						arr=[{type:ent,result:'no info!'}];
					}
				}
				
				if(!ent){
					let n=1;
					for(var key in cont){
						arr.push({'No.':n++,'key':key,'value':cont[key]});
					}
				}
				//console.log(arr);
				this.items=arr;
				this.rows=arr.length;
	
			})
			.catch(e => console.log("Oops, error", e));
		}
	},
	//computed:{
		//rows:function(){
			//return this.itmes.length;
		//}
	//}
	components:{
		BButtonGroup,
		BButton
	}
}

</script>

<style scoped>
@import '../assets/bootstrap.min.css'; 
@import '../assets/bootstrap-vue.min.css'; 

</style>